// /app/page.js

"use client"

import Link from "next/link"
import { Button } from "@/components/ui/button"
import { LogOut, User, Home } from "lucide-react"

export default function MainPage() {
  // 클라이언트 측에서만 localStorage에 접근
  const accessToken = typeof window !== 'undefined' ? localStorage.getItem("accessToken") : null

  const handleLogout = () => {
    // 실제로는 여기에 백엔드 로그아웃 API 호출 로직을 추가해야 합니다.
    
    // 로컬 저장소 토큰 삭제
    localStorage.removeItem("accessToken")
    localStorage.removeItem("refreshToken")
    
    // 로그인 페이지로 리다이렉트
    window.location.href = "/login" 
  }

  return (
    <div className="min-h-screen flex flex-col items-center justify-center p-4 bg-gray-100">
      <div className="w-full max-w-lg p-10 bg-white rounded-xl shadow-2xl space-y-8 text-center">
        <Home className="h-14 w-14 text-primary mx-auto" />
        
        <h1 className="text-4xl font-extrabold text-gray-800">Next Me 서비스 메인</h1>
        
        {accessToken ? (
          <div className="space-y-6">
            <p className="text-xl font-semibold text-primary flex items-center justify-center">
              <User className="h-6 w-6 mr-2" />
              로그인 성공!
            </p>
            <p className="text-gray-600">환영합니다. 이제 Next Me의 모든 서비스를 이용할 수 있습니다.</p>
            
            <Button 
              onClick={handleLogout} 
              className="w-full bg-red-500 hover:bg-red-600 h-12 text-lg"
            >
              <LogOut className="h-5 w-5 mr-2" />
              로그아웃 하기
            </Button>
          </div>
        ) : (
          <div className="space-y-4">
            <p className="text-lg text-red-500 font-medium">
              로그인이 필요합니다.
            </p>
            <Button asChild className="w-full h-12 text-lg">
              <Link href="/login">
                로그인 페이지로 이동
              </Link>
            </Button>
          </div>
        )}
        
      </div>
    </div>
  )
}